import Vue from 'vue';
import firebase from 'firebase';
import Buefy from 'buefy';
import 'buefy/dist/buefy.css';
import VueResource from 'vue-resource';
import {
  library,
} from '@fortawesome/fontawesome-svg-core';
import {
  faCalendar,
} from '@fortawesome/free-solid-svg-icons';
import {
  FontAwesomeIcon,
} from '@fortawesome/vue-fontawesome';
import router from './router';
import App from './App.vue';

library.add(faCalendar);

Vue.component('font-awesome-icon', FontAwesomeIcon);

Vue.use(Buefy, {
  defaultIconPack: 'fa',
});
Vue.use(VueResource);

Vue.config.productionTip = false;


const config = {
  apiKey: 'AIzaSyB1KU4uBhahk89s_B8D2oydmKa23994B8c',
  authDomain: 'campusbuddy-dd715.firebaseapp.com',
  databaseURL: 'https://campusbuddy-dd715.firebaseio.com/',
  projectId: 'campusbuddy-dd715',
};

router.beforeEach((to, from, next) => {
  if (!to.meta.protected) { // route is public, don't check for authentication
    next();
  } else { // route is protected, if authenticated, proceed. Else, login
    const unsubscribe = firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        next();
      } else {
        router.push('/login');
      }
    });
    unsubscribe();
  }
});

new Vue({
  router,
  created() {
    firebase.initializeApp(config);
  },
  render: h => h(App),
}).$mount('#app');
